.container {
  display: flex;
  width: 100%;
}
.videoDetail {
  /* height: 100%; */
  display: flex;
  flex-direction: column;
}
.metadata {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.title {
  width: 100%;
  margin-top: 20px;
  margin-bottom: 10px;
  font-size: 1.125rem;
  padding-bottom: 20px;
  border-bottom: 1px solid gray;
}
.channel {
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
}
.channel_image {
  width: 50px;
  height: 50px;
  border-radius: 50px;
}
.channel_text {
  margin-left: 15px;
}
.channel_title {
  margin-bottom: 5px;
  font-size: 0.9rem;
  font-weight: 540;
}
.channel_subscribers {
  font-size: 0.8rem;
  color: gray;
}
.description {
  width: 100%;
  margin-top: 10px;
  white-space: pre-wrap;
  font-size: 0.9rem;
  line-height: 20px;
}
.actived {
  width: 100%;
  margin-top: 10px;
  white-space: pre-wrap;
  font-size: 0.9rem;
  line-height: 20px;
}

.inactived {
  width: 100%;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  font-size: 0.9rem;
  line-height: 20px;
}

.moreBtn {
  margin-top: 15px;
  color: gray;
  cursor: pointer;
  font-size: 0.9rem;
  width: fit-content;
}
.moreBtn:hover {
  cursor: pointer;
}

.playlist {
  display: flex;
  flex-wrap: wrap;
}

/* tablet(768~1024px) */
@media (min-width: 768px) and (max-width: 1024px) {
  .container {
    flex-direction: column;
    align-items: center;
  }
  .videoDetail {
    width: 100vw;
    margin-bottom: 20px;
  }
  .video {
    width: 100vw;
    height: 56vw;
    position: sticky;
    top: 70px;
    z-index: 1;
  }
  .metadata {
    padding-left: 10px;
    padding-right: 10px;
  }
  .playlist {
    width: 100vw;
    margin: 0;
  }
}
/* mobile(~768px) */
@media (max-width: 768px) {
  .container {
    flex-direction: column;
  }
  .videoDetail {
    width: 100vw;
    margin-bottom: 20px;
  }
  .video {
    width: 100vw;
    height: 56vw;
    /* position: sticky; */
    /* top: 70px; */
    z-index: 1;
  }
  .metadata {
    width: 100vw;
    padding-left: 10px;
    padding-right: 10px;
  }
  .playlist {
    width: 100vw;
    margin: 0;
  }
}
/* pc (1025px 이상) */
@media (min-width: 1025px) {
  .container {
    padding-top: 20px;
    padding-left: 40px;
  }
  .videoDetail {
    width: 50%;
    margin-right: 25px;
  }
  .video {
    width: 100%;
    height: 28.125vw;
  }
  .playlist {
    width: 40%;
  }
}
